.menu {
  width: 90vw;
  max-width: 1200px;
  height: 40px;
  margin: auto;
  background-image: linear-gradient(#777, #bbb, #777);
  margin: 20px auto;
}

.menu__link {
  padding: 0 80px;
  text-decoration: none;
  font-size: 25px;
  font-weight: bold;
  color: white;
}

.menu__link_active {
  color: #333;
}